import React, { useEffect, useCallback, useState } from "react"
import { TimelineMax } from "gsap"
import "../styles/navMen.scss"

const Menu = (props) => {
  let animate
  let tl2 = new TimelineMax({paused: true})
  
  const hover = useCallback((enter) => {
    let items = animate.childNodes
    let duration2 = .1
    tl2.to(animate, duration2 * 2,{
        x: "-10%",
    })
    tl2.to(items[0], duration2, {
      width: "90%",
    }, 0)
    tl2.to(items[1], duration2, {
      width: "100%",
    }, `-= ${duration2 * .5}`)
    tl2.to(items[2], duration2, {
      width: "80%",
    }, `-= ${duration2 * .5}`)
    if(enter === true){
        tl2.play();
    }else{
        tl2.reverse(duration2 * 2);
    }
  })
  useEffect(() => {
    let tl = new TimelineMax({ pause: true })
    let items = animate.childNodes
    let duration = 0.2
    tl.to(items[2], duration, {
      width: "100%",
      opacity: 1,
    })
    tl.to(
      items[1],
      duration,
      {
        width: "100%",
        opacity: 1,
      },
      `-=${duration * 0.8}`
    )
    tl.to(items[1], 0, {
      marginLeft: "auto",
    })
    tl.to(items[1], duration * 0.8, {
      width: "80%",
    })
    tl.to(
      items[0],
      duration,
      {
        width: "100%",
        opacity: 1,
      },
      `-=${duration * 1.15}`
    )
    tl.play()
  })
  return (
    <div
      onMouseEnter={()=>hover(true)}
      onMouseLeave={() => hover(false)}
      ref={div => (animate = div)}
      className="navMen"
      onClick = {props.toggleNav}
    >
      <div></div>
      <div className="extend"></div>
      <div></div>
    </div> 
  )
}

export default Menu
